import React from 'react'
import "./style/contact.css"

function Contact() {
  return (
    <>
      <div className='contact'>
        <div className='C-bg'></div>
        <div className="C-img">
          {/* <img src={"https://images01.nicepage.com/c461c07a441a5d220e8feb1a/7e8527524b5058aba0934eab/-min.jpg"} /> */}
        </div>
        <div className='C-lines'>
          {/* <img src='https://images01.nicepage.com/aa/49/aa49e5bbcb321c65e1d4ba4d4afa599b.png' /> */}
        </div>
        <div className="Contact-card">
          <h1>Contact Us</h1>
          <form className='C-form'>
            <input type={"text"} placeholder="Enter Your Full Name" id='Cname'/>
            <input type={"email"} placeholder="Enter a Valid Email Address" id='Cemail'/>
            <textarea placeholder='Enter Message'></textarea>
            <input type={"submit"} placeholder="Enter Message" value={"SUBMIT"} className="C-sub"/>
          </form>
        </div>
        <div className='C-info'>
          <div className='C-call'>
            <img src='https://cdn-icons-png.flaticon.com/512/597/597177.png'/>
             <p>+12 0012 12222</p>
             <p>+12 0012 12222</p>
          </div>
          <div className='C-locat'>
            <img src='https://cdn-icons-png.flaticon.com/512/535/535239.png'/>
              <p>121 Rock Sreet, 21 Avenue,<br/> New York, NY 92103-9000</p>
          </div>
          <div className='C-eml'>
              <img src='https://cdn-icons-png.flaticon.com/512/2099/2099199.png'/>
             <p>iconDesign@gmail.com</p>
          </div>
        </div>
      </div>
    </>

  )
}

export default Contact;
