import React from 'react'
import { Link } from 'react-router-dom'
import "./style/login.css"

function Login() {
  return (
    <>
      {/* start login page  --- there are mistaks of keywords*/}
      <div className='sign'>
        <div className='sign-img'>
          <div className='signpage'>
            <h2>Wellcome Back,</h2>
            <form>
              <input type={"text"} placeholder={"Email Address"} id="L-email" />
              <input type={"password"} placeholder={" Your Password"} id="L-pass"/>
             <Link to={"/forgot"} className="fortext" ><span>Forgot Password ?</span></Link>
              <input type={"submit"} value={"Log In"} id="L-submit"/>
            </form>
            
            <p className='L-para'>Don't have any Account ?</p>
            <Link to={"/signup"}  className="L-sign"><span>SignUp</span></Link>
          </div>
          <div className='L-with'>
            <p>Login With</p>
            <div className='L-glg'><Link><img src='https://cdn-icons-png.flaticon.com/512/300/300221.png' width={"30px"}/></Link></div>
            <div className='L-face'><Link><img src='https://cdn-icons-png.flaticon.com/512/3670/3670032.png' width={"30px"}/></Link></div>
            <div className='L-email'><Link><img src='https://cdn-icons-png.flaticon.com/512/732/732200.png' width={"30px"}/></Link></div>
          </div>
        </div>

      </div>

    </>

  )
}

export default Login
