import React from 'react'
import { Link, NavLink } from 'react-router-dom'
import "./style/signup.css"


const Signup = () => {
  return (
    <div className='login'>
      <div className='user'>
        <div className='login-img'>
        </div>
        <div className='valid'>
          <h2>Register and Enjoy your peace :)</h2>
          <form>
            <div>
              {/* <label>Full Name:</label><br /> */}
              <input type={"text"} id={"fname"} name={"fname"} placeholder="Enter Full Name" /><br />
            </div>


            <div className='num'>

              <input type={"tel"} id={"tel"} name={"tel"} placeholder="Enter Mobail No." /><br />

              <input type={"tel"} id={"pin"} name={"pin"} placeholder="Enter Pin Code" /><br />
            </div>

            <div>
              {/* <label>Email Address:</label><br /> */}
              <input type={"email"} id={"email"} name={"email"} placeholder="Email Address" /><br />
            </div>

            <div>
              {/* <label>Password:</label><br /> */}
              <input type={"password"} id={"pass"} name={"pass"} placeholder="Password" /><br />
              <NavLink to={"/forgot"} style={{textDecoration:"none"}}><p id='fpass'>Forgot Password?</p></NavLink>
            </div>

            <input type={"submit"} id="sub" name='sub' value={"Sign Up"} />
          </form>

          <span id='ls'>Already have an account</span>
          <NavLink to={"/login"} ><button>Log In</button></NavLink>

          <p id='or'>or</p>
          <span id='s-with'>Sign Up With</span>
          <div className='gAct'>
            {/* <p></p> */}
            <NavLink className='S-img'><img src='https://cdn-icons-png.flaticon.com/512/300/300221.png' width={"23px"} /></NavLink>
            <NavLink className='S-img'><img src='https://cdn-icons-png.flaticon.com/512/3670/3670032.png' width={"23px"} /></NavLink>
            <NavLink className='S-img' ><img src='https://cdn-icons-png.flaticon.com/512/732/732200.png' width={"23px"} /></NavLink>
          </div>
        </div>
      </div>

    </div>
  )
}

export default Signup
