import React from 'react';
import { NavLink, Link, Outlet } from 'react-router-dom';
import { useCart } from 'react-use-cart';
import "./style/navbar.css"


const Navbar = () => {
    const {
        isEmpty,
        totalUniqueItems,
        items,
        updateItemQuantity,
        removeItem,
    } = useCart();

    window.addEventListener("scroll", function () {
        let navbar = document.getElementById("main-nav");

        if (window.pageYOffset >= 50) {
            navbar.classList.add("sticky");
        }
        else {
            navbar.classList.remove("sticky");
        }
    })

    window.addEventListener("scroll", function () {
        let ulnav = document.getElementById("ulsticky");

        if (window.pageYOffset >= 50) {
            ulnav .classList.add("top-nav");
        }
        else if(window.pageYOffset <= 50){
            ulnav.classList.remove("top-nav");
        }
        // else {
        //     ulnav.classList.remove("top-nav");
        // }
    })

    

    return (
        <>
            <div class="top-contact">
                <div class="top-links">
                    <ul>
                        <li><Link href='#'><i class="fa-brands fa-facebook"></i></Link></li>
                        <li><Link href='#'><i class="fa-brands fa-twitter"></i></Link></li>
                        <li><Link href='#'><i class="fa-brands fa-instagram"></i></Link></li>
                        <li><Link href='#'><i class="fa-brands fa-github"></i></Link></li>
                    </ul>
                </div>
                <div class="line">
                    <p>FLASH SALE THIS WEEK - OFF 50% ITEMS <NavLink href='#'>CHECK IT NOW</NavLink></p>
                </div>
                <div class="call">
                    <address>
                        <Link>
                            <i class="fa-solid fa-envelope"></i> support@la-studioweb.com
                        </Link>
                        <Link> <i class="fa-solid fa-phone"></i> +1 854.584.85</Link>
                    </address>
                </div>
            </div>
            <header className="header sticky" id='main-nav'>
                <label for="check" class="checkbtn">
                    <img src='https://cdn-icons-png.flaticon.com/512/9441/9441639.png' width={"40px"} />
                </label>
                <input type="checkbox" id="check" />

                <div class='logo'>
                    <NavLink id='hpage' to={"/"} ><img src="../asset/icon/ceiling-light.png" width="80px" alt="" />
                        <h2> IconDesign</h2></NavLink>
                </div>
                {/* <div className='search-item'>
                    <form action="">
                        <input type="search" name="pname" id="pname" placeholder="Search your faviroute decor..." />
                        <input type="submit" value="Search" />
                    </form>
                </div> */}
                <nav className='nav'>
                    <ul className='top-nav' id='ulsticky'>
                        <li id="active"><NavLink to="/" >Home</NavLink></li>
                        <li><NavLink to={"/services"} >Services</NavLink></li>
                        <li><NavLink to={"/catagory"} >Catagory</NavLink></li>
                        <li><NavLink to={"/contactus"} >Contact US</NavLink></li>
                        <Outlet />
                    </ul> 
                </nav>
                <div class='cart'>
                    <NavLink><img src='https://cdn-icons-png.flaticon.com/128/2107/2107845.png' width={"30px"} /></NavLink>
                    <NavLink to={"/cart"} id="cart-img" ><img src='https://cdn-icons-png.flaticon.com/128/3514/3514491.png' width={"30px"} /><sup> {totalUniqueItems}</sup></NavLink>
                    <NavLink to={"/signup"} id="user"><img src='https://cdn-icons-png.flaticon.com/128/456/456212.png' width={"30px"} /></NavLink>
                </div>
            </header>

        </>
    )
}

export default Navbar
